//
// Front Page
//

.home {
    
    .section-hero {
        background: #ffffff;
        position: relative;
        overflow: hidden;
        visibility: hidden;
        
        
        @include breakpoint(xlarge) {
            &:before {
                content: '';
                display: block;
                position: absolute;
                bottom: -2px;
                left: 0;
                width: 100%;
                height: 13%;
                background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 100 13' preserveAspectRatio='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpolygon fill='%23ffffff' points='0 0, 0 13,40 13'/%3E%3C/svg%3E");
                z-index: 9;
            }
            
            &:after {
                content: '';
                display: block;
                position: absolute;
                bottom: -2px;
                right: 0;
                width: 100%;
                height: 13%;
                background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 100 8' preserveAspectRatio='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpolygon fill='%23ffffff' points='60 8, 100 0,100 8'/%3E%3C/svg%3E");
                z-index: 9;
            }
        }
        
        
        @supports (clip-path: polygon(100% 0, 100% 88%, 64% 100%, 36% 100%, 0 88%, 0 0)) {
            &:before,
            &:after {
                display: none;   
            }
            clip-path: polygon(100% 0, 100% 88%, 64% 100%, 36% 100%, 0 88%, 0 0);
        }
        
        
        
        /*height: calc(100vw *(9/16));
        min-height: rem-calc(400);
                
        @include breakpoint(xlarge) {
            height: calc(100vw *(9/16));
            max-height: calc(80vh - 65px);
        }
        */
        
        .slick {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            height: 100%;
            max-height: 100%;
            
            .slick-list,
            .slick-track,
            .slick-slide {
                height: 100%;
            }
                         
            .slick-slide {
                background-size: cover;
                background-position: center;
                
                img {
                    opacity: 0;
                    visibility: hidden;
                }
            }
        }
                
        .grid-x {
                                    
            // Flexbox Hack - CSS target only IE11 and IE10 (media query)
            @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
                &:after {
                  content: '';
                  display: block;
                  min-height: inherit;
                  font-size: 0;
                }
            }
            
        } 
        
        
        .hero-content {
            background-color: rgba(18,55,89,0.7);
            padding: rem-calc(60 0);
            position: relative;
            z-index: 2;
            text-align: center;
            
            @include breakpoint(large) {
                padding: rem-calc(120 0);
            }
            
            h1 {
                color: #fff; 
                margin-bottom: 0;  
            }
            
            h4 {
                color: #ECECEC;
                margin: rem-calc(20 auto 0);  
                max-width: rem-calc(900);
            }
            
            > *:last-of-type {
                margin-bottom: 0;
            }
            
            .button {
                margin-top: rem-calc(50); 
                min-width: rem-calc(248);
            } 
            
            .play-video {
                @include play;
                @include pulse;
                margin: 50px auto 0;
                display: block;
                width: 50px;
                height: 50px;
            }
        }        
    }
    
    
    .section-services {
        
        padding-top: rem-calc(100);        
        
        .grid {
            
            header {
                margin-bottom: rem-calc(10);
                text-align: center;
                
                span {
                    display: block;
                    margin-bottom: rem-calc(10);   
                }
                
                @include breakpoint(large) {
                    
                    text-align: left;
                    
                    > * {
                        display: inline-block; 
                        vertical-align: top;  
                    }
                    
                    span {
                        display: inline-block; 
                        margin-right: rem-calc(15);  
                        margin-bottom: 0; 
                    }
                }
            }
            
            footer {
                text-align: center;   
            }
            
            ul {
                @include bullets($orange);
                margin-bottom: rem-calc(50);
                
                display: inline-block;
                
                @include breakpoint(medium-large) {
                    display: block;
                }
                
                li {
                    text-align: left;
                    margin-top: rem-calc(10);
                    
                    
                    a {
                        color: $dark-gray;
                        letter-spacing: 0.7px;
                        font-family: $barlow;
                        font-size: rem-calc(18);
                        font-weight: 500;
                        text-transform: uppercase;   
                    }
                }
            }
        }
    }
    
    .section-customers {
        
        background: $light-gray;
        margin-top: rem-calc(25);
        padding: rem-calc(75 0 50);
        
        header {
            text-align: center; 
            margin-bottom: rem-calc(15); 
        }
        
        .logos {
            @include list-reset;
            display: flex;
            align-items: center;
            justify-content: center;
            
            flex-wrap: wrap;
            
            @include breakpoint(large) {
                flex-wrap: no-wrap;
            }
            
            li {
                margin-bottom: rem-calc(25);   
            }
            
            a {
                dispay: block;   
            }
            
            span {
                display: block;
                padding: rem-calc(15);
            }
        }
    }  
    
    
    .section-featured-post {
        background-color: $light-gray;
        margin-top: rem-calc(55);
        padding: rem-calc(75 0 40);  
        
        header {
            margin-bottom: rem-calc(30);   
        }
        
        .cell {
            padding-left: rem-calc(40);
            padding-right: rem-calc(40) ;
        }
        
        .thumbnail {
            background-size: cover;
            background-positoin: center;
            display: block;
            padding-top: 100%;
            border-radius: 50%;
            box-shadow: $box-shadow;
        }
        
        .panel {
            position: relative;            
        }
        
        h3 {
            color: $orange;   
        }
    }
}