// Service

.post-type-archive-fleet {
    
    .single-fleet {
        .fancybox-navigation,
        .fancybox-infobar {
            display: none;   
        }
    }
    
    .fleet-ajax {
        max-width: rem-calc(2560);   
    }
    
    
    .section-hero {
                
        .wrap {
            background-color: rgba(18,55,89,0.7);   
        }
        
        .grid-x {
            &:before {
              content: "";
              width: 1px;
              margin-left: -1px;
              float: left;
              height: 0;
              padding-top: 46%;
            }
            &:after { /* to clear float */
              content: "";
              display: table;
              clear: both;
            }
        }
        
        .hero-content {
            
            text-align: left;
            
            h1 {
                color: #fff; 
                margin-bottom: 0;  
                
                @include breakpoint(large) {
                    margin-top: -100px;   
                }
            }

        }
    }
    
    
    .facetwp-template {
        margin-top: rem-calc(60);
        @include breakpoint(large) {
            margin-top: rem-calc(140);
            @include xy-grid-layout(2, '.cell', true, 10);
            @include xy-gutters(10, 'margin', $negative:true); // negative for the nested grid
            
            .cell {
                margin-bottom: rem-calc(10);   
            }
        }   
    }
    
    
    article.fleet-column {
        position: relative;
        display: flex;
        //align-items: center;
        justify-content: center;
        background-size: cover;
        background-position: center;
        box-shadow: 0 11px 12px 0 rgba(0,0,0,0.1);
        
        &:before {
          content: "";
          width: 1px;
          margin-left: -1px;
          float: left;
          height: 0;
          padding-top: 65%;
        }
        &:after { /* to clear float */
          content: "";
          display: table;
          clear: both;
        }
        
        .panel {
            padding: rem-calc(30);
            text-align: center; 
            
            @include breakpoint(large) {
                padding: rem-calc(60);
            }
            
            &:before {
                content: '';
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
            }
            
            h4,
            h3 {
                position: relative;
                z-index: 2;  
                
                color: #fff; 
            }
            
            h3 {
                margin-bottom: 0;   
            }
            
            a {
                display: block;
                
                &:before {
                    content: '';
                    position: absolute;
                    top: 0;
                    right: 0;
                    bottom: 0;
                    left: 0;
                    z-index: 2;  
                }
            }
        }
        
        .panel:before {
            background-color: rgba(0,0,0,0.7);
        }
        
        &:hover .panel:before {
            background-color: rgba(0,0,0,0.8);
        }
    }
    
    .site-main {
        padding-top: 0;
        @include breakpoint(large) {
            //padding-top: rem-calc(140);   
        }
     }    
    
    .site-content {
        
        .content-area {
            position: relative;
            z-index: 3;
        }
     
        .widget-area {
            background-color: $light-gray;
            padding-top: rem-calc(30);
            padding-right: rem-calc(30);
            
            h2 {
                margin-bottom: rem-calc(30);   
            }
            
            @include breakpoint(large) {
                position: relative;
                z-index: 2;
                
                &:before {
                    content: '';
                    position: absolute; 
                    top: 0;
                    right: -120px;
                    width: 50vw;  
                    height: 12vw;
                    background-color: $light-gray;  
                    transform-origin: top right;
                    transform: rotate(13deg);
                    z-index: -1;
                }
                
                &:after {
                    content: '';
                    position: absolute; 
                    top: 0;
                    right: -120px;
                    width: 50vw;  
                    height: 100%;
                    background-color: $light-gray;
                    z-index: -1;
                }
            }
        }
        
    }
    
}